import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import './TopBar.css'; 
import logo from './logo-lg-screen.svg';
import photoButtonImage from './500px-button-logo.svg';
import linkedinButtonImage from './linkedin-button-logo.svg';

class TopBar extends Component {
  
  constructor(props) {
    super(props);
    this.state = {fixed: false};
    this.handleScroll = this.handleScroll.bind(this);
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);    
  }
  
  handleScroll(event){
    let scrollTop = event.srcElement.body.scrollTop
    console.log(scrollTop);
    if (window.pageYOffset > 144){
      this.setState({fixed:true})
    }
  }

  render() {
    return (
      <div id='top-bar' className='top-bar-fixed'>
        <div id='top-bar-container'>
          <div id='top-bar-nav'>
            <nav>
              <ul>
                <li><NavLink to='/' activeClassName='nav-link-text'><span className='nav-link-text'>Me</span></NavLink></li>
                <li><NavLink to='/texts'><span className='nav-link-text'>Texts</span></NavLink></li>
                <li><NavLink to='/things'><span className='nav-link-text'>Things</span></NavLink></li>
                <li><NavLink to='/micro'><span className='nav-link-text'>Micro</span></NavLink></li>
              </ul>
            </nav>
          </div>
          <div id='top-bar-logo-frame'>
            <img id='top-bar-logo' src={logo} alt='logo' />
          </div>
          <div id='top-bar-share'>
            <a href='' className='nav-share-link'><img src={photoButtonImage} alt='500x' /></a>
            <a href='' className='nav-share-link'><img src={linkedinButtonImage} alt='linkedin' /></a>
          </div>
        </div>
        <div id='top-bar-border'></div>
      </div>
    );
  };
};

export default TopBar;



